.movieCard {
  display: flex;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
      7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
}
.movieCardImg {
  margin-right: 10px;
}

.movieCardGenresList {
  display: flex;
  gap: 5px;
}
.infoContainer{
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
  7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  
}
.movieCardBtnList {
  margin-top: 10px;
  display: flex;
}
.movieCardBtnLink {
  color: #fff;
}
.btn {
  width: 114px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  text-align: center;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
  background: rgb(165, 149, 190);
  background: linear-gradient( 0deg, rgb(172 177 156) 0%, rgb(165 118 115) 100% );
  border: none;
}
.btn:not(:first-child){
  margin-left: 20px;
} 
.btn:before {
  height: 0%;
  width: 2px;
}
.btn:hover {
  box-shadow: 4px 4px 6px 0 rgba(255, 255, 255, 0.5),
    -4px -4px 6px 0 rgba(116, 125, 136, 0.5),
    inset -4px -4px 6px 0 rgba(255, 255, 255, 0.2),
    inset 4px 4px 6px 0 rgba(0, 0, 0, 0.4);
}
